.footer_res{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.footer_res img{
    width:35%;
    margin-top: 40px;
}

.footer_res ul{
    display:flex;
    padding:20px 0px 15px 0px;
}

.footer_res li{
    font-size: 13px;
    font-weight: bold;
    padding:0px 15px 0px 15px;
    color: #6b6b6b;
}

.footer_res li:first-child{
    border-right: 1px solid #aaaaaa;
    color: #a6845e;
}

.company_res{
    font-size:13px;
    font-weight: bold;
    margin-bottom:15px;
}

.address_res{
    width:70%;
    font-size:12px;
    font-weight: bold;
    color:#6b6b6b;
    text-align: center;
    margin-bottom:15px;
}

.about_item_res{
    display:flex;
    justify-content: center;
}

.about_res dl{
    display:flex;
    align-items: center;
    justify-content: center;
    margin:7px 0px 7px 0px;
    padding:0px 5px 0px 5px;
}

.about_res dl dt{
    font-size:13px;
    font-weight: bold;
}

.about_res dl dd{
    font-size:12px;
    font-weight: bold;
    color:#6b6b6b;
}

.copyright_res{
    font-size:12px;
    font-weight: bold;
    text-align: center;
    margin:15px 0px 40px 0px;
    color:#6b6b6b;
}