.yotube_img_container{
    position: relative;
    height:980px;
}

.youtube_img{
    position: absolute;
    top:12%;
    left:50%;
    transform: translateX(-50%);
    width:1100px;
    height:620px;
    overflow: hidden;
}

.youtube_img_bg{
    height:100%;
}

.youtube_img div{
    display:flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top:50%;
    left:50%;
    transform: translateX(-50%) translateY(-50%);
    width:100px;
    height: 100px;
    border-radius: 50%;
    font-size:35px;
    color:#fff;
    background-color: #a6845e;
    cursor:pointer
}

.youtube_img div svg{
    padding-left:8px;
}

.sns_container{
    position: absolute;
    bottom:12%;
    left:50%;
    transform: translateX(-50%);
    display:flex;
    justify-content: center;
    width:1100px;
    cursor: pointer;
}

.sns_container div{
    display:flex;
    align-items: center;
    height:30px;
    padding:0px 80px 0px 80px;
}

.sns_container div h2{
    font-size:24px;
    margin-right:10px;
    color:#aaaaaa;
}

.sns_container div svg{
    font-size:70px;
    color: #fff;
}

.sns_container div:first-child{
    border-right:1px solid #aaaaaa;
}

.sns_container div:last-child{
    border-left:1px solid #aaaaaa;
}


