.review{
    position: relative;
}

.review_container_header{
    position: absolute;
    top:0;
    margin-left:190px;
    padding-top:150px;
}

.review_text{
    display:flex;
    align-items: center;
}

.review_text div{
    width:70px;
    height:1px;
    background-color: #fff;
    margin-right:20px;
}

.review_text h3{
    font-size:14px;
    color: #fff;
}

.review_container_header h2{
    font-size:31px;
    margin:30px 0px 30px 0px;
    color:#fff;
}

.review_container_header p{
    font-weight: bold;
    color:#c9c9c9;
}


/*text_container*/
.text_container{
    position: absolute;
    top:400px;
    width:100%;
    margin: 0px 190px 0px 190px;
    display:flex;
}

.text_item1{
    width:380px;
    height:387px;
    padding:0px 50px 0px 50px;
    background-color: rgba(10, 10, 10, 0.6);
}

.text_item1 h2{
    height:58px;
    margin-top:50px;
    font-size:25px;
    color:#fff;
}

.text_item1 p{
    height:119px;
    margin:30px 0px 50px 0px;
    font-size:15px;
    font-weight: bold;
    color:#c9c9c9;
}

.review_btn_container{
    display:flex;
    justify-content: space-between;
}

.review_btn_container h4{
    font-size:14px;
    color:#fff;
}

.review_btn_container button{
    font-size:12px;
    border:none;
    background-color: transparent;
    color:#fff;
    cursor: pointer;
}

