.service_layout_res{
    width:90%;
    margin:auto;
    overflow: hidden;
}

.slide_banner_res{
    display:flex;
    width:100%;
    margin:auto;
    margin-top:30px;
    transform : translateX(0px)
}

.slide_banner_res img{
    width:100%;
    height:450px;
}

.service_item_one_res{
    position: relative;
    width:100%;
    flex-shrink: 0;
}

.service_text1_res{
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%, -50%);
    width: 70%;
    height:70%;

}

.service_text1_res strong{
    font-size:40px;
    color: #eec692;
    padding-bottom: 10px;
    border-bottom: 3px solid #fff;
}

.service_text1_res h2{
    margin:40px 0px 40px 0px;
    font-size:17px;
    color: #eec692;
}

.service_text1_res p{
    font-size:10px;
    font-weight: bold;
    color:#fff;
    height:100px;
}

.slide_banner_res button{
    display:flex;
    align-items: center;
    color:#fff;
    background-color: transparent;
    border:none;
}

.slide_banner_res button div{
    font-size:11px;
    margin-right:5px;
}

.slide_banner_res button svg{
    font-size:11px;
}

