.service_img_container {
    width:80%;
    margin:auto;
    margin-top:50px;
    padding-bottom:100px;
    display:flex; 
}

.service_item1{
    position: relative;
    height:600px;
    overflow: hidden;
}

.service_text1{
    position: absolute;
    top:50%;
    left:50%;
    transform: translateX(-50%) translateY(-50%);
    width:380px;
    height:420px;
}

.service_text1 strong{
    font-size:50px;
    color:#e8c18f;
    border-bottom: 3px solid #e8c18f;
}

.service_text1 h2{
    font-size:28px;
    color:#e8c18f;
    margin:50px 0px 50px 0px;
}

.service_text1 button{
    display:flex;
    border: none;
    color: #fff;
    background-color: transparent;
    cursor: pointer;
}

.service_text1 button div{
    margin-right:5px;
}

.service_text1 p{
    color:#fff;
    margin:80px 0px 80px 0px;
    height:95px;
}

