.review_header_res{
    position: relative;
    width: 100%;
    height: 650px;
    overflow: hidden;
}

.review_header_res img{
    position: absolute;
    top:0;
    z-index: -1;
    width: 150%;
    height: 100%;
}

.container_header_review_res{
    width:90%;
    margin:auto;
    display:flex;
    flex-direction: column;
    padding-top:40px;
}

.review_text_res{
    display:flex;
    align-items: center;
}

.review_bar_res{
    width:30px;
    height:2px;
    background-color:#fff;
    margin-right:10px;
}

.review_text_res h3{
    font-size:12px;
    color:#fff;
}

.container_header_review_res h2{
    font-size:25px;
    margin:20px 0px 15px 0px;
    color:#fff;
}

.container_header_review_res p{
    font-size:13px;
    font-weight: bold;
    color:#bdbdbd;
}

.review_layout_res{
    width:90%;
    margin:auto;
    padding:30px 0px 0px 0px;
    overflow: hidden;
}

.review_container_res{
    display: flex;
    width:100%;
}

.text_item1_res{
    display:flex;
    width:100%;
    flex-direction: column;
    background-color: rgba(10, 10, 10, 0.6);
    flex-shrink: 0;
}

.text_item1_res h2{
    height:42px;
    font-size:19px;
    color:#fff;
    padding:40px 40px 0px 40px;
}

.text_item1_res p{
    height:98px;
    font-size:11px;
    font-weight: bold;
    color:#bdbdbd;
    margin:35px 0px 35px 0px;
    padding:0px 40px 0px 40px;
}

.review_btn_container_res{
    display:flex;
    justify-content: space-between;
    align-items: center;
    margin:15px 0px 15px 0px;
    padding:0px 40px 30px 40px;
}

.review_btn_container_res h4{
    font-size:12px;
    color:#fff;
}

.review_btn_container_res button{
    font-size:12px;
    border: none;
    color:#fff;
    background-color: transparent;
}

.review_btn_res{
    display:flex;
    justify-content: center;
    padding:40px 0px 0px 0px ;
}

.review_btn_res button{
    width:10px;
    height: 10px;
    border: 1px solid #fff;
    border-radius: 50%;
    padding:0;
    background-color: transparent;
    margin:0px 5px 0px 5px;
}

.review_btn_res button:first-child{
    background-color: #fff;
}