.faq_res{
    position:relative;
    width:100%;
    height:564px;
    overflow: hidden;
    background-color: #f3f3f3;
    padding:60px 0px 60px 0px;
}

.faq_res img{
    position: absolute;
    top:0;
}

.faq_container_res{
    position: relative;
    width:90%;
    margin:auto;
    z-index: 1;
    text-align: center;
}

.faq_viewmore_res{
    display:flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom:10px;
    border-bottom: 3px solid black;
}

.faq_viewmore_res h1{
    font-size:38px;
    font-family: 'Noto Sans KR', sans-serif;
}

.faq_viewmore_res button{
    display:flex;
    font-size:11px;
    border:none;
    color:#616161;
    background-color: transparent;
}

.faq_viewmore_res button div{
    margin-right:5px;
}

.faq_list_item_res{
    display:flex;
    align-items: center;
    padding:12px 18px 12px 18px;
    border-bottom:1px solid #c9c9c9;
}

.faq_list_item_res div{
    width:23px;
    height:20px;
    border-radius: 50%;
    font-size:13px;
    font-weight: bold;
    text-align: center;
    padding-top:3px;
    color:#fff;
    background-color: #a6845e;
}

.faq_list_item_res h3{
    width:80%;
    margin:auto;
    font-size:11px;
    text-align: left;
}

.faq_advice_res{
    padding:14px 50px 14px 50px;
    margin:30px 0px 65px 0px;
    font-weight: bold;
    color:#fff;
    background-color: #a6845e;
    border: none;
}

.news_container_res{
    width:90%;
    margin:auto;
}

.news_viewmore_res{
    display:flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom:10px;
}

.news_viewmore_res h1{
    font-size:38px;
    font-family: 'Noto Sans KR', sans-serif;
}

.news_viewmore_res button{
    display:flex;
    font-size:11px;
    border:none;
    color:#616161;
    background-color: transparent;
}

.news_viewmore_res button div{
    margin-right:5px;
}

.news_list_res{
    display:flex;
}

.news_item_res{
    border-top:3px solid black;
}

.news_item_res p{
    font-size:11px;
    font-weight: bold;
    color:#b0b0b0;
    padding:15px 0px 15px 0px;
}

.news_item_res h2{
    font-size:14px;
}

.news_list_res div:first-child{
    margin-right:10px;
}
.news_list_res div:last-child{
    margin-left:10px;
}