.contact_res{
    background-color: #f3f3f3;
    padding-bottom:50px;
}
.contact_container_res{
    width:90%;
    margin:auto;
    margin-top:20px;
    border: 1px solid #d5d5d5;
}

.contact_container_item_res{
    display:flex;
    align-items: center;
    justify-content: space-between;
    padding:25px 30px 25px 30px;
    border-bottom: 1px solid #d5d5d5;
    background-color: #fff;
}

.contact_container_item_res:last-child{
    border-bottom:none;
}

.contact_container_item_res h2{
   font-size:15px;
   color:#a6845e;
   margin-bottom:15px;
}

.contact_container_item_res p{
    font-size:12px;
    color: #363636;
}