.footer{
    width:80%;
    margin:auto;
    display:flex;
    justify-content: space-between;
    padding:30px 0px 30px 0px;
}

.footer_container h5{
    font-size:16px;
    margin:20px 0px 20px 0px;
}

.footer_container p{
    font-size:13px;
    font-weight: bold;
    margin:20px 0px 20px 0px;
}

.footer_container div{
    display:flex;
}

.footer_container dl{
    display:flex;
    font-size:14px;
    padding:0px 10px 0px 10px;
    border-left:1px solid #aaaaaa;
}

.footer_container dl:first-child{
    padding-left:0px;
    border-left: none;
}

.footer_container dl dt{
    font-weight: bold;
}

.office{
    display:flex;
    align-items:end;
    margin:20px 0px 20px 0px;
    cursor: pointer;
}

.office h4{
    padding:0px 10px 0px 10px;
}

.office h4:first-child{
    color:#a6845e;
}

.office h4:last-child{
    padding-right:0;
    border-left:1px solid #aaaaaa;
}