.Businessarea{
    background-color: #f3f3f3;
}

/*Businessarea_header*/
.container_header{
    width:80%;
    margin:auto;
    padding-top:150px;
}

.text{
    display:flex;
    align-items: center;
}

.text div{
    width:70px;
    height:1px;
    background-color: #a6845e;
    margin-right:20px;
}

.text h3{
    font-size:14px;
    color: #a6845e;
}

.container_header h2{
    font-size:31px;
    margin:30px 0px 30px 0px;
}

.container_header p{
    font-weight: bold;
    color:#8e8e8e;
}

/*Businessarea_menu*/
.Businessarea_menu{
    width:80%;
    margin:auto;
    margin-top:10px;
}

.Businessarea_menu ul{
    display:flex;
    justify-content: right;
}

.Businessarea_menu li{
    font-size:18px;
    font-weight: bold;
    padding:0px 20px 0px 20px;
    color:#838383;
}

.Businessarea_menu li:hover{
    transition: all 0.5;
    color:#a6845e;
}


/*img*/
.img_container{
    width:80%;
    margin:auto;
    margin-top:70px;
    display:grid;
    grid-template-areas: 
    "A B C D"
    "E F G H";
    row-gap: 10px;
    padding-bottom:120px;
}

.img_container img{
    width: 370px;
    height:370px;
}

.img_item1{
    position: relative;
}

.hide_content{
    display:flex;
    flex-direction: column;
    justify-content: center;
    position: absolute;
    width:370px;
    height:370px;
    top:0%;
    text-align: center;
    color:#fff;
    background-color: #674e3e;
    opacity: 0;
}

.hide_content h2{
    font-size:25px;
}

.hide_content p{
    font-size:15px;
    font-weight: bold;
    margin:20px 0px 20px 0px;
}


.btn_container button{
    display:inline-flex;
    padding:10px 20px 10px 20px;
    border: 1px solid #fff;
    color:#fff;
    background-color: transparent;
    margin:0px 3px 0px 3px;
    cursor: pointer;
}

.hide_content button:hover{
    transition: all 0.5s;
    border: 1px solid #a6845e;
    background-color: #a6845e;
}


.btn_container button div{
    padding:0px 5px 0px 5px;
}

