.yotube_img_container_res{
    position: relative;
    width:100%;
    overflow: hidden;
}

.yotube_img_container_res img{
    width:200%;
}

.youtube_img_res{
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-90% , -50%);
}

.youtube_img_res img{
    width:180%;
}

.youtube_img_res div{
    display:flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top:50%;
    left:100%;
    transform: translate(-75%, -50%);
    width:70px;
    height:70px;
    border-radius: 50%;
    font-size:25px;
    color:#fff;
    background-color: #a6845e;
}

.sns_container_res{
    position: absolute;
    bottom:10%;
    display:flex;
    justify-content: center;
    align-items: center;
    width:100%;
}

.sns_container_res img{
    width:20%;
    margin:0px 15px 0px 15px;
}

.sns_container_res svg{
    font-size:35px;
    color:#fff;
    margin:0px 15px 0px 15px;
}

