/*데스크탑 버전 css*/

/*animation*/
@keyframes text_up {
    0%{
        opacity: 0;
        transform: translateY(40px);
    }
    100%{
        opacity: 1;
        transform: translateY(0px);
    }
}

@keyframes pointer {
    0%{
        opacity: 1;
        top: 5px
    }

    100%{
        opacity: 0;
        top:12px;
    }
}


/*header nav*/
.header{
    display:flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    width: 100%;
    z-index: 99;
    padding:40px 0px 20px 0px;
    transform: translateY(-100px);
    transition: all 1.2s;
}

.header ul{
    display:flex;
}

.header li{
    color: #fff;
}

/*nav*/
.nav{
    margin:0px 130px 0px 130px;
}

.nav li{
    font-size:18px;
    font-weight: bold;
    padding:0px 30px 0px 30px;
}

/*login*/
.login{
    margin-right:50px;
}

.login li{
    font-size:13px;
    padding:0px 10px 0px 10px;
}

.login li:first-child{
    border-right: 1px solid #fff;
}


/*sns*/
.sns li{
    font-size:25px;
    padding:0px 10px 0px 10px;
}

/*Youtube video*/
.youtube_video{
    position: relative;
    width:100vw;
    height: 100vh;
    overflow: hidden;
    pointer-events: none; 
}

.youtube_video iframe{
    width:100vw;
    height: 100vh;
    transform: scale(120%);
    filter: brightness(80%);
}

/*start animation Text*/
.animation_text{
    display:flex;
    flex-direction: column;
    align-items: center;
    position:absolute;
    width:100%;
    top:50%;
    left:50%;
    transform: translateX(-50%) translateY(-50%);
    color:#fff;
}

.animation_text h2{
    font-size:50px;
    font-family: 'Noto Sans KR', sans-serif;
    margin:10px 0px 10px 0px;
    opacity: 0;
}


.animation_text button{
    font-size:17px;
    padding:20px 60px 20px 60px;
    margin-top:40px;
    border: 1px solid #fff;
    color:#fff;
    background-color:transparent;
    cursor: pointer;
    opacity: 0;
}

/*scroll down icon*/
.scroll_down{
    display:flex;
    align-items: center;
    position: absolute;
    bottom:10%;
    left:50%;
    transform: translateX(-50%) translateY(200px);
    transition: all 1s;
    color:#fff;
}

.circle{
    position: relative;
    width:16px;
    height:24px;
    border: 2px solid #fff;
    border-radius: 42%;
    margin-right:8px;
}

.pointer{
    position: absolute;
    top:5px;
    left:50%;
    transform: translateX(-50%);
    width: 3px;
    height: 3px;
    border-radius: 50%;
    background-color: #fff;
    animation: pointer 1.2s infinite;
}

.scroll_down h4{
    font-size: 12px;
    font-weight:lighter;
}