.businessare_res{
    background-color: #f3f3f3;
}

.container_header_res{
    display:flex;
    flex-direction: column;
    width:90%;
    margin:auto;
    padding-top:40px;
}

.text_res{
    display:flex;
    align-items: center;
}

.bar_res{
    width:30px;
    height:2px;
    background-color:#a6845e;
    margin-right:10px;
}

.text_res h3{
    font-size:12px;
    color:#a6845e;
}

.container_header_res h2{
    font-size:20px;
    margin:20px 0px 15px 0px;
}

.container_header_res p{
    font-size:11px;
    font-weight: bold;
    color:#555555;
}

.Businessarea_menu_res{
    width:90%;
    margin:auto;
    margin-top:30px;
}

.Businessarea_menu_res ul{
    display:flex;
    justify-content: center;
    padding-bottom:10px;
}

.Businessarea_menu_res ul li{
    font-size:12px;
    font-weight: bold;
    padding:0px 5px 0px 5px;
    color:#878787;
    border-right:1px solid #bdbdbd;
}

.Businessarea_menu_res ul li:last-child{
    border:none;
}

.area_img_container_res{
    display:grid;
    grid-template-areas: 
    "A B"
    "C D"
    "E F"
    "G H";
    width:90%;
    margin:auto;
    padding-top:10px;
    padding-bottom:50px;
}

.area_img_container_res img{
    width:100%;
    height: 160px;
    vertical-align: bottom;
}

.img_item_res_one{
    position: relative;
    grid:A;
}

.hide_content_one_res{
    position: absolute;
    top:0;
    display:flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width:100%;
    height:0px;
    overflow: hidden;
    color:#fff;
    background-color: rgba(106, 76, 58, 0.8);
}

.btn_container_res{
    display:flex;
    align-items: center;
    justify-content: center;
}

.hide_content_one_res h2{
    font-size:14px;
    margin:30px 0px 0px 0px;
    text-align: center;
}

.btn_container_res button{
    display:flex;
    padding:10px 14px 10px 14px;
    margin:0px 3px 0px 3px;
    font-size: 11px;
    color:#fff;
    background-color: transparent;
    border: 1px solid #fff;
}

.btn_container_res button div{
    padding-right:3px;
}

