.faq{
    position: relative;
    height:658px;
    padding: 150px 0px 150px 0px;
    overflow: hidden;
    background-color: #f3f3f3;
}

.faq img{
    position: absolute;
    top:0;
}

.faq_container{
    width:80%;
    height:360px;
    margin:auto;
    display:flex;
    justify-content: space-between;
}

.faq_viewmore h1{
    font-size:65px;
    margin-bottom:30px;
}

.faq_viewmore button{
    position: relative;
    display:flex;
    border: none;
    background-color: transparent;
    cursor: pointer;
    z-index: 999;
}

.faq_viewmore button div{
    margin-right:8px;
}

.faq_list{
    position: relative;
    z-index: 1;
}

.faq_list_item{
    display:flex;
    align-items: center;
    width:1050px;
    padding:20px 40px 20px 40px;
    border-bottom:1px solid #c9c9c9;
}

.faq_list_item:first-child{
    border-top: 3px solid black;
}

.faq_list_item div{
    width:35px;
    height:30px;
    border-radius: 50%;
    font-size:17px;
    font-weight: bold;
    text-align: center;
    padding-top:5px;
    color:#fff;
    background-color: #a6845e;
}

.faq_list_item h3{
    width:940px;
    margin:0px 70px 0px 25px;
    font-size:18px;
    cursor: pointer;
}

.faq_list_item h3:hover{
    color:#a6845e;
    transition: all 0.3s;
}

.faq_list_item svg{
    cursor: pointer;
}

.faq_advice{
    float: right;
    margin-right:190px;
    font-size:17px;
    font-weight: bold;
    padding:18px 70px 18px 70px;
    border:none;
    color:#fff;
    background-color: #a6845e;
    cursor: pointer;
}

.news_container{
    width:80%;
    height:360px;
    margin:auto;
    margin-top:180px;
    display:flex;
    justify-content: space-between;
}

.news_list{
    display:flex;
    justify-content: space-between;
    width:1130px;
}

.news_item{
    width:303px;
    padding-top:20px;
    border-top:3px solid black;
}

.news_item p{
    font-size:12px;
    font-weight: bold;
    color:#858585;
    margin-bottom:20px;
}

.news_item h2{
    position: relative;
    font-size:25px;
    z-index: 1;
    cursor: pointer;
}