.header_res{
    position: fixed;
    display:flex;
    align-items: center;
    justify-content: space-evenly;
    width:100%;
    z-index: 999;
    padding:20px 0px 0px 0px ;
    transform: translateY(-100px);
    transition: all 1s;
}

.header_res img{
    width:163px;
}

.sns_res{
    display:flex;
}

.sns_res img{
    width:32px;
}

.sns_res li{
    padding:0px 12px 0px 12px;
}

.sns_res li svg{
    color: #fff;
    font-size:17px;
}

.header_res div svg{
    color:#fff;
    font-size:17px;
}

.youtube_video_res{
    position: relative;
    width:100%;
    height:100vh;
    overflow: hidden;
}

.youtube_video_res iframe{
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%, -50%);
    width:100%;
    height:100%;
    min-width:177.7vh;
    pointer-events: none; 
}

.animation_text_res{
    position: absolute;
    top:50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color:#fff;
}

.animation_text_res h2{
    white-space: nowrap;
    font-size:25px;
    font-family: 'Noto Sans KR', sans-serif;
    margin:5px 0px 5px 0px;
    opacity: 0;
}

.animation_text_res button{
    margin-top:10px;
    padding:12px 35px 12px 35px;
    background-color: transparent;
    border:1px solid #fff;
    color:#fff;
    opacity: 0;
}

.scroll_down_res{
    position: absolute;
    bottom:5%;
    left:50%;
    transform: translateX(-50%) translateY(200px);
    display:flex;
    align-items: center;
    transition: all 1s;
}

.circle_res{
    position: relative;
    width:10px;
    height:16px;
    border: 2px solid #fff;
    border-radius: 42%;
    margin-right:8px;
}

.scroll_down_res h4{
    font-size:10px;
    font-weight: lighter;
    color:#fff;
}

.nav_hide_res{
    display:flex;
    flex-direction: column;
    position: absolute;
    top:0px;
    width:100%;
    height:100vh;
    background-color: #fff;
    z-index: 999999999;
    max-height: 0px;
    overflow: hidden;
}

.nav_hide_item{
    display:flex;
    width:100%;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #d3d3d3;
    padding:15px 0px 15px 0px;
}

.nav_hide_item ul{
    display:inline-flex;
    margin-left:20px;
}

.nav_hide_item ul li{
    font-size:11px;
    font-weight: bold;
    padding:0px 15px 0px 15px;
}

.nav_hide_item ul li:first-child{
    padding-left:0px;
    border-right:1px solid #d3d3d3;
}

.nav_hide_item h3{
    margin-left:20px;
}

.nav_hide_item div{
    margin-right:20px;
    color:#d3d3d3;
     font-size:20px;   
}

.nav_hide_item:first-child svg{
    color:#a6845e;
    font-size:20px;
}

.nav_hide_item:first-child{
    border-bottom: 2px solid black;
}
