.contact{
    background-color: #f3f3f3;
    padding-bottom:100px;
}

.contact_container{
    width:80%;
    margin:auto;
    display:flex;
}

.contact_container div{
    width:385px;
    height:309px;
    text-align: center;
    background-color: #fff;
    border: 1px solid #d5d5d5;
    cursor: pointer;
}

.contact_container div img{
    margin-top:65px;
}

.contact_container div h2{
    margin:30px 0px 30px 0px;
    font-size:24px;
    color:#a6845e;
}

.contact_container div p{
    font-size:15px;
    font-weight: 400;
    color: #363636;
}